.skills {
  &__wrapper {
    display: flex;
    flex-direction: column;
  }
  &__title {
    margin: 100px;
    text-align: center;
    font-size: 45px;
    font-weight: 900;
  }
  img {
      margin-top: 30px;
      height: 150px;
    }
  &__ratings {
    margin: 120px auto;
    display: grid;
    grid-template: repeat(2, minmax(70px, auto)) / repeat(3, 350px);
    column-gap: 196px;
    row-gap: 40px;
    &-item {
      display: grid;
      grid-template: minmax(21px, auto) 14px / 200px 35px;
      justify-content: space-between;
      align-content: space-between;
    }
    &-title {
      position: relative;
      padding: 2px 0 0 13px;

      &:before {
        content: '';
        position: absolute;
        z-index: -1;
        top: -25%;
        left: 0;
        width: 36px;
        height: 36px;
        border-radius: 100%;
        background: rgba(113, 109, 100, 0.75);
      }
    }
    &-counter {
      font-size: 14px;
      line-height: 21px;
    }
    &-line {
      width: 350px;
      height: 14px;
      background-color: #E8E4DE;
      border-radius: 4px;
      grid-column: 1 / 3;
      span {
        display: block;
        height: 100%;
        background-color: rgba(113, 109, 100, 0.75);
        border-radius: 4px;
      }
    }
  }
}

