@import url('https://fonts.googleapis.com/css2?family=Montserrat:wght@400;500;900&display=swap');

* {
  margin: 0;
  padding: 0;
  font-family: 'Montserrat';
}


.main-page {
  height: 100vh;
  &__wrapper {
    display: flex;
    flex-direction: row-reverse;
    justify-content: space-between;
  }

  &__photo {
    margin-top: 0;
    width: 50vw;
    height: 100vh;
    object-fit: cover;
  }

  .about-me {
    margin: 50px 0 0 245px;
    width: 50vw;
    &__subtitle {
      margin-top: 160px;
      font-weight: 900;
      font-size: 35px;
      line-height: 43px;
      color: gray;
    }
    &__title {
      margin-top: 22px;
      font-weight: 900;
      font-size: 52px;
      line-height: 63px;
    }
    &__descr {
      margin-top: 47px;
      width: 570px;
      height: 297px;
      font-weight: 400;
      font-size: 16px;
      line-height: 24px;
    }

  }
}
