@media (max-width: 1469px) {
    .skills {
        &__ratings {
            grid-template: repeat(3, minmax(70px, auto))/repeat(2, 350px);
        }
    }
    .main-page {
        .about-me {
            width: 48vw;
            &__descr {
                width: 46vw;
                margin-right: 70px;
            }
        }
    }

    .projects {
        &__info {
            margin: 0 0 0 100px;
        }
    }
}

@media (max-width: 1080px) {
    .main-page {
        .about-me {
            &__descr {
                line-height: 25px;
                font-size: 20px;
            }
        }
    }
}
    .my-contacts {
        padding: 80px 0 0 80px;
    }

    .projects {
        padding: 170px 80px 17px 110px;
    }


@media (max-width: 921px) {
    .main-page {
        .about-me {
            &__subtitle {
                margin-top: 0;
            }
        }
    }

    .skills {
        &__ratings {
            grid-template: repeat(6, minmax(70px, auto))/repeat(1, 350px);
        }
    }

    .projects {
        padding: 170px 80px 17px 0;
        height: 600px;

       &__info {
           &-descr {
               font-size: 22px;
           }
       }
       &__swipe {
           display: block;
           margin: 125px 0 0 80px;
           text-align: center;
       }
       .slick-arrow {
           display: none;
       }
    }

    .my-contacts {
        padding: 80px 0 0 25px;

        &__title {
            font-size: 45px;
        }

        &__btns {
            margin-top: 35px;
            width: 80vw;
            button {
                width: 150px;
            }
        }

        &__block {
            h3 {
                font-size: 20px;
            }
        }
    }
}

@media (max-width: 768px) {
    .my-contacts {
        &__btns {
            button {
                width: 125px;
            }
        }

    }
    .projects {
        &__info {
            &-descr {
                font-size: 21px;
            }
        }
    }
}

@media (max-width: 688px) {
    .my-contacts {
        &__btns {
            button {
                width: 100px;
            }
        }

    }

    .projects {
        &__descr {
            font-size: 20px;
        }
    }

    .projects {
        &__info {
            &-descr {
                font-size: 20px;
            }
        }
    }
}

@media (max-width: 576px) {
    .main-page {
        .about-me {
            &__subtitle {
                line-height: 28px;
            }
            &__title {
               font-size: 40px;
               line-height: 1;
            }
        }
    }

    .skills {
       img {
           height: 125px;
       }
    }

    .projects {
        padding: 125px 29px 17px 0;
        &__info {
            &-title {
                font-size: 40px;
            }
        }
    }

    .skills {
        img {
            height: 100px;
        }
    }

    .my-contacts {
        &__btns {
            button {
                width: 90px;
            }
        }
    }
}

@media (max-width: 476px) {
    .main-page {
        .about-me {
            &__subtitle {
                font-size: 23px;
                line-height: 23px;
            }
            &__title {
                font-size: 29px;
            }
        }
    }

    .projects {
        height: 215vw;
        padding: 0 0 0 0;
        &__img {
            float: none;
            width: 100vw;
            height: 70vw;
        }
        &__info {
            display: flex;
            flex-direction: column;
            align-items: center;
            margin: 30px 0 0 0;
            width: 100vw;
            &-descr {
                margin: 20px 0 100px 0;
            }
        }
        &__swipe {
            margin: 80px 0 0 0;
            font-size: 19px;
        }
    }

    .my-contacts {
        padding: 80px 0 0 10px;
        &__block {
            h3 {
                font-size: 17px;
            }
        }
        &__btns {
            width: auto;
            display: block;
            button {
                width: 150px;
                margin: 10px;
            }
        }

    }

}

@media (max-width: 354px) {
    .main-page {
        .about-me {
            &__subtitle {
                font-size: 20px;
            }
            &__title {
                font-size: 25px;
            }
        }
    }

    .skills {
        &__ratings {
            &-item {
                grid-template: minmax(21px, auto) 14px/200px 60px;
            }
            &-line {
                width: 90%
            }
        }
    }

    .projects {

        &__swipe {
            margin: 40px 0 0 0;
            font-size: 19px;
        }
    }

    .my-contacts {
        display: flex;
        flex-direction: column;
        align-items: center;
    }
}