.my-contacts {
  height: 400px;
  padding: 80px 0 0 170px;
  &__title {
    font-weight: 900;
    font-size: 52px;
    line-height: 63px;
  }
  &__block {
    margin-top: 24px;
    h3 {
      display: flex;
      margin-top: 20px;
      font-weight: 500;
      font-size: 24px;
      line-height: 151.4%;
      color: #A70707;
    }
  }
  &__svg {
    &-mail {
      margin-top: 6px;
      margin-right: 15px;
    }
    &-phone {
      margin-right: 23px;
    }
  }
  &__btns {
    display: flex;
    margin-top: 54px;
    justify-content: space-between;
    width: 795px;
    button {
      width: 188px;
      height: 48px;
      border: 2px solid #A70707;
      border-radius: 50px;
      font-weight: 500;
      font-size: 15px;
      line-height: 151.4%;
      color: #A70707;
      background: #FFF;
      cursor: pointer;
    }
    #disabled {
      border: 2px solid gray;
      color: gray;
      cursor: default;
    }
  }
}