.projects {
  background: #716D64;
  height: 770px;
  padding: 170px 80px 17px 200px;
  &__swipe {
    font-weight: 300;
    font-size: 22px;
    color: #FFFFFF;
    display: none;
  }
  &__info {
    width: 23vw;
    margin: 30px 0 0 100px;
    &-title {
      font-weight: 900;
      font-size: 47px;
      line-height: 45px;
      color: #FFFFFF;
    }
    &-descr {
      margin: 20px 0 160px 0;
      font-weight: 300;
      font-size: 25px;
      height: 120px;
      line-height: 151.4%;
      color: #FFFFFF;
    }
    &-btn {
      width: 188px;
      border: 2px solid #FFFFFF;
      border-radius: 50px;
      font-weight: 500;
      height: 48px;
      font-size: 15px;
      line-height: 151.4%;
      background: #716D64;
      color: #FFFFFF;
      cursor: pointer;
    }
  }
  &__img {
    float: right;
    width: 33vw;
    height: 33vw;
    object-fit: cover;
  }
}

.slick-arrow {
  width: 67px;
  margin-top: 405px;
}

.slick-next {
  margin-right: 110px;
}
